//--|ðŸ Š Section.launch.scss ðŸ ˆ|--//
@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  $border-styling: 2px dashed white;
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 4rem 1fr 4rem / repeat(3, 1fr); //--|ðŸ Š rows / columns ðŸ ˆ|--//
  div {
    height: 100%;
    overflow: hidden;
    display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
    transition: grid-template 0.75s ease-in-out;
    header {
      display: flex;
      color: white;
      align-items: center;
      justify-content: center;
      grid-area: 2 / 1 / 3 / 2; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      transition: color 0.75s ease-in-out;
      h3 {
        font-weight: bold;
        transition: margin 0.75s ease-in-out;
      }
    }
    picture {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: 3 / 1 / 4 / 2; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      img {
        grid-area: 2 / 2 / 3 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
        transition: margin 0.75s ease-in-out;
      }
    }
    footer {
      display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
      overflow: visible; //--|ðŸ Š Allows for the child to overflow ðŸ ˆ|--//
      position: relative; //--|ðŸ Š Establish a positioning context ðŸ ˆ|--//
      grid-area: 4 / 1 / 6 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      transition: grid-template 0.75s ease-in-out;

      p {
        margin: 0 4rem;
        color: white;
        position: absolute; //--|ðŸ Š Take it out of the normal flow ðŸ ˆ|--//
        text-align: justify;
        transition: opacity 0.75s ease-in-out;
      }
      nav {
        display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
        grid-area: 2 / 1 / 3 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
        grid-template: 1fr auto 1fr / 1fr 3fr 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
        button {
          grid-area: 2 / 2 / 3 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
        }
      }
    }

    &[id*='overtime'] {
      grid-area: 1 / 1 / 4 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      background: radial-gradient(
        circle,
        rgba(Palettes.$trackDay-dark, 1) 0%,
        rgba(Palettes.$trackDay-medium, 1) 50%,
        rgba(Palettes.$trackDay-light, 1) 100%
      );
      /*
      border-width: 6px;
      border-style: dashed;
      border-color: white;
      */
      border-right: $border-styling;

      header h3 {
        filter: drop-shadow(
          rgba(Palettes.$trackDay-dark, 1) 0 0 4px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      picture img {
        filter: drop-shadow(
          rgba(Palettes.$trackDay-dark, 1) 0 0 1rem
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      footer {
        p {
          filter: drop-shadow(Palettes.$trackDay-dark 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
        nav {
          filter: drop-shadow(
            Palettes.$trackDay-light 0 0 1rem
          ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
      }
    }
    &[id*='ticketing'] {
      grid-area: 1 / 2 / 4 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      border-left: $border-styling;
      border-right: $border-styling;
      background: radial-gradient(
        circle,
        rgba(Palettes.$logTicket-dark, 1) 0%,
        rgba(Palettes.$logTicket-medium, 1) 50%,
        rgba(Palettes.$logTicket-light, 1) 100%
      );
      header h3 {
        filter: drop-shadow(
          rgba(Palettes.$logTicket-dark, 1) 0 0 4px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      picture img {
        filter: drop-shadow(
          rgba(Palettes.$logTicket-dark, 1) 0 0 1rem
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      footer {
        p {
          filter: drop-shadow(
            Palettes.$logTicket-dark 0 0 8px
          ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
        nav {
          filter: drop-shadow(
            Palettes.$logTicket-light 0 0 1rem
          ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
      }
    }
    &[id*='hyperlink'] {
      grid-area: 1 / 3 / 4 / 4; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      border-left: $border-styling;
      background: radial-gradient(
        circle,
        rgba(Palettes.$findLink-dark, 1) 0%,
        rgba(Palettes.$findLink-medium, 1) 50%,
        rgba(Palettes.$findLink-light, 1) 100%
      );

      header h3 {
        filter: drop-shadow(
          rgba(Palettes.$findLink-dark, 1) 0 0 4px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      picture img {
        filter: drop-shadow(
          rgba(Palettes.$findLink-dark, 1) 0 0 1rem
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }

      footer {
        p {
          filter: drop-shadow(Palettes.$findLink-dark 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
        nav {
          filter: drop-shadow(
            Palettes.$findLink-light 0 0 1rem
          ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
        }
      }
    }
  }

  $large-image: 50%;
  $small-image: 25%;
  div[class*='downplay'] {
    grid-template: 4rem 1fr auto 1fr 4rem / 1fr;
    header {
      h3 {
        margin: 0;
      }
    }
    picture {
      img {
        margin: 0;
        width: $large-image; //--|ðŸ Š height: auto; ðŸ ˆ|--//
        animation: growImage 0.75s ease-in-out; // Added animation property
      }
    }
    footer {
      grid-template: 1fr 6rem 1fr 4rem / 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
      p {
        opacity: 0;
        /* filter: blur(4px); */
      }
    }
  }
  div[class*='highlight'] {
    grid-template: 4rem 0fr auto 1fr 4rem / 1fr;
    header {
      h3 {
        margin: 3rem 0 3rem 0;
      }
      &[class*='track-day'] {
        color: Palettes.$trackDay-light;
      }
      &[class*='log-ticket'] {
        color: Palettes.$logTicket-light;
      }
      &[class*='find-link'] {
        color: Palettes.$findLink-light;
      }
    }
    picture {
      img {
        width: $small-image; //--|ðŸ Š height: auto; ðŸ ˆ|--//
        margin: 0 0 3rem 0;
        animation: shrinkImage 0.75s ease-in-out; // Added animation property
      }
    }
    footer {
      grid-template: 1fr 6rem 0fr 4rem / 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
      p {
        opacity: 1;
        /* filter: blur(0); */
      }
    }
  }
  @keyframes shrinkImage {
    0% {
      width: $large-image;
    }
    100% {
      width: $small-image;
    }
  }
  @keyframes growImage {
    0% {
      width: $small-image;
    }
    100% {
      width: $large-image;
    }
  }
}
@mixin mobile-portrait {
}

section[class*='launch'] {
  display: grid;
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
}
